<style>.serif{font-family:EB Garamond,serif}.box-title{font-family:Mali,sans-serif;border-top:1pt solid #333;text-align:center;text-transform:capitalize;font-variant:small-caps}.info-box textarea{width:100%;height:auto;border:1pt solid #333;resize:none}.thick-separator{border-bottom:1pt solid #333;height:3pt;padding:0;margin:0}textarea{font-family:Shadows Into Light,cursive;font-weight:300;font-style:normal;font-size:12pt}</style>

<script>
    import getNewCharacter from '../../model/character'
    import PageHeader from '../shared/PageHeader'
    export let character = getNewCharacter();
</script>

